import { TImage, TLink } from "@/types";
import Link from "next/link";
import { StrapiImage } from "../strapi-image";
import { Button } from "../../ui/button";


export interface IHeroSectionProps {
  id: number;
  documentId: string;
  __component: string;
  heading: string;
  subHeading: string;
  image: TImage;
  link: TLink[];
}

export function HeroSection({ data }: { data: IHeroSectionProps }) {
    if (!data) return null;

    const { heading, subHeading, image, link } = data;

  console.dir(data, { depth: null });
  return (
    <header className="relative h-screen overflow-hidden">
      <StrapiImage
        alt={image.alternativeText ?? "no alternative text"}
        className="absolute inset-0 object-cover w-full h-full aspect/16:9"
        src={image.url}
        height={1080}
        width={1920}
      />
      <div className="relative px-8 py-4 z-10 flex flex-col items-center justify-center h-full bg-black/50 text-center">
        <h1 className="relative py-4 max-w-5xl overflow-hidden font-stretch-75% text-brand-pink text-2xl md:text-4xl lg:text-6xl font-black italic font-sans">{heading}</h1>
        <p className="mt-4 font-bold text-white font-stretch-90% max-w-2xl text-lg md:text-lg lg:text-xl">{subHeading}</p>
        <div className="flex gap-4 mt-8">
            {link.map((btn) => (
                <Link key={btn.id} href={btn.href}>
                    <Button className= "mt-8 inline-flex border-brand-red items-center italic text-xl text-brand-red justify-center px-10 py-8 font-extrabold bg-brand-pink shadow hover:bg-brand-pink/90 transition-colors" variant="secondary" >
                        {btn.label}
                    </Button>
                </Link>
            ))}
        </div>
      </div>
    </header>
  )
}